import { derived, writable } from 'svelte/store';

import subjects from './earl/subjectStore.js';

const mockedSamples1 = [
  {
    title: 'Home',
    description: 'http://www.example.org/'
  },
  {
    title: 'Form',
    description: 'http://www.example.org/contact/'
  },
  {
    title: 'Content',
    description: 'http://www.example.org/content/'
  },
  {
    title: 'Collection Listing',
    description: 'http://www.example.org/collection/'
  },
  {
    title: 'Collection Detail',
    description: 'http://www.example.org/collection/detail/'
  },
  {
    title: 'Process',
    description: 'http://www.example.org/process-page/'
  },
  {
    title: 'Process (Step 1)',
    description: 'Filling in the form as part of Process'
  },
  {
    title: 'Process (Step 2)',
    description: 'Review the form. Part of Process'
  },
  {
    title: 'Process (Step 3)',
    description: 'Confirmation step. After submitting the form as part of Process'
  },
  {
    title: 'Absurd!long_unshort#shortn@me_20201111',
    description: 'For the purpose of showing what happens with non-short names...'
  },
  {
    title: 'PDF',
    description: 'http://www.example.org/yearly_report_2020.pdf'
  }
].map((sample, index) => {
  sample.ID = 2 + index;
  sample.type = 'WebPage';

  return subjects.create(sample);
});

const mocked2 = [
  {
    title: 'Random (1)',
    description: 'http://www.example.org/foo/bar/random-page-1/'
  },
  {
    title: 'Random (2)',
    description: 'http://www.example.org/bar/foo/randoms/random-2'
  }
].map((sample, index) => {
  sample.ID = 2 + mockedSamples1.length + index;
  sample.type = 'WebPage';

  return subjects.create(sample);
});

export const initialSampleStore = {
  STRUCTURED_SAMPLE: [],
  RANDOM_SAMPLE: []
};

const sampleStore = writable({
  STRUCTURED_SAMPLE: mockedSamples1,
  RANDOM_SAMPLE: mocked2
});

export const allSamples = derived(sampleStore, ($sampleStore) => {
  return [...$sampleStore['STRUCTURED_SAMPLE'], ...$sampleStore['RANDOM_SAMPLE']];
});

export default sampleStore;
